.Reservation {
    margin-top: 1rem;
    margin-bottom: 2rem;
    background-color: white ;
    -ms-flex-line-pack: center;
        align-content: center;
    left: 0;
    right: 0;
    text-align: center;
    overflow-x: hidden;
}

.Reservation_Title {
    font-size: 2em;
}

.Reservation_SubText{
    font-size: 1.2em;
}

@media only screen and (max-width: 493px) {
    .Reservation_Title {
        font-size: 1.5em;
    }

    .Reservation_SubText{
        font-size: 1em;
    }

    .LineDivider {
        width: 50%;
    }
  }

  



