.Services_Container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    max-width: 100%;
    padding: 2rem;
}

.Services_Title {
    font-size: 2em;
    background-color: white;
}

.Services_Link {
    font-weight: 600;
    color: rgb(146, 43, 33);
    text-decoration: none;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out
}

.Services_Link:hover {
    border-bottom: 2px dotted rgb(146, 43, 33);
}

.Services_Text {
    -ms-flex-order: 2;
        order: 2;
    text-align: center;
    width: auto;
    padding: 1rem;
    -ms-flex-item-align: center;
        align-self: center;
    line-height: 1.5;
    font-size: 1.2em;
}


.Services_ImageOne {
    -ms-flex-order: 1;
        order: 1;
}

.Services_Image {
    border: 2px solid rgb(146, 43, 33);
}

@media only screen and (max-width: 1200px) {
    .Services_ImageOne {
        display: none;
    }

    .Services_ImageTwo {
        display: none;
    }
}

.Services_ImageTwo {
    -ms-flex-order: 3;
        order: 3;
}

.Services_Image {
    height: auto;
    width: 35%;
}

@media only screen and (max-width: 493px) {
    .Services_Title {
        font-size: 1.5em;
    }

    .Services_Text{
        font-size: 1em;
    }

    .Services_Container {
        margin: -3rem 0 -3rem 0;
    }
  }






